@media all and (max-width: 1200px) {
    .container {
        width: 100%;
    }
    .repear-devices-element {
        &:nth-of-type(1) {
          width: 90%;
        }
        &:nth-of-type(2) {
            width: 90%;
        }
        &:nth-of-type(3) {
            width: 90%;
        }
    }
    .why-we-greed {
        align-items: center;
        grid-gap: 50px;
    }
    .after-h1,h1,.recalling {
        margin-left: 5%;
    }
}
@media all and (max-width: 1000px) {
    .why-we-element,
    .comfort-grid-element  {
        margin: 0;
          &:nth-of-type(3) {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 2;
            grid-row-end: 3;
            width: 100%;
            h4 {
                margin-top: 260px;
            }
        }
    }
    .comford-grid {
        margin-top: 50px;
        grid-gap: 0px;
    }
    .footer-garanty {
        .logo {
            display: none;
        }
    }
}
@media all and (max-width: 850px) {
    .main-menu-hide .hideEightHundred {
        display: none;
    }
    .main-menu-show {
        .container {
            height: 75%;
            padding: 50px 0;
        }
        .hideEightHundred {
            display: block;
        }
    }
    .menu-show-hide-hide {
        &:nth-of-type(1) {
            display: block;
        }
    }
    .menu-show-hide:nth-of-type(1) {
        background: url(../img/list.svg) center/contain no-repeat;
        a {
            display: none;
        }
    }
    .menu-show-show:nth-of-type(1) {
        background: url(../img/WeAreBlack.png) center/contain no-repeat;
        a {
            display: block;
        }
    }
    .menu-show-show {
        display: block;
        height: 50px;
        &:nth-of-type(1) {
            height: 50px;
        }
    }
    .main-menu-show {
        .container,
        ul {
            flex-direction: column;
            &:nth-of-type(1) {
                margin-bottom: 30px;
            }
        }
    }
}
@media all and (max-width: 750px) {
    h1 {
        font-size: 40px;
        text-align: center;
    }
    .after-h1 {
        font-size: 28px;
        text-align: center;
        display: block;
    }
    .after-h1, .recalling, h1 {
        margin-left: 0;
    }
    .recalling {
        margin: 0 auto;
    }
    .why-we-greed {
        margin-bottom: 70px;
        grid-gap: 100px;
    }
    .why-we-element,
    .repear-devices-element,
    .comfort-grid-element  {
        margin: 0;
          &:nth-of-type(1) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 1;
            grid-row-end: 2;
            width: 100%;
            h4 {
                margin-top: 260px;
            }
        }
    }
    .why-we-element,
    .repear-devices-element,
    .comfort-grid-element  {
        margin: 0;
          &:nth-of-type(2) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
            width: 100%;
            h4 {
                margin-top: 260px;
            }
        }
    }
    .why-we-element,
    .repear-devices-element,
    .comfort-grid-element  {
        margin: 0;
          &:nth-of-type(3) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
            width: 100%;
            h4 {
                margin-top: 260px;
            }
        }
    }
    .repear-devices-element:nth-of-type(1),
    .repear-devices-element:nth-of-type(2),
    .repear-devices-element:nth-of-type(3) {
        height: 200px;
    }
    .repear-devices-grid {
        grid-gap: 50px;
        margin: 50px 0;
    }
    .repear-devices-element:nth-of-type(1) {
        background: url(../img/choice-iphone.png) right / contain no-repeat #fff;
    }
    .repear-devices-element:nth-of-type(2) {
        background: url(../img/choice-ipad.png) right / contain no-repeat #fff;
    }
    .repear-devices-element:nth-of-type(3) {
        background: url(../img/choice-mac-mini.png) right / contain no-repeat #fff;
    }
    .question-span {
        align-items: center;
        margin: 20px 0;
        flex-direction: column;
        span {
            font-size: 23px;
            margin: 5px 0;
        }
    }
    .questions img {
        height: 320px;
        top: 0;
    }
    .wr-what-we-can a, .wr-what-we-can p, h4 {
        text-align: center;
    }
    .repear-devices-element:nth-of-type(1) p {
        text-align: left;
        margin: 70px;
    }
    .repear-devices-element:nth-of-type(1),
    .repear-devices-element:nth-of-type(2),
    .repear-devices-element:nth-of-type(3) {
        width: 90%;
        margin: 0 auto;
    }
    .question-span {
        width: 100%;
    }
}
@media all and (max-width: 600px) {
    .number-logo {
        width: 100%;
    }
    header {
        height: max-content;
    }
    .questions img {
        display: none;
    }
    .footer-links {
        height: max-content;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    .footer-garanty-message {
        width: 90%;
        top: 20px;
        left: 5%;
    }
    footer {
        background: #393939;
    }
    .recalling {
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        height: 150px;
        margin: 0;
        width: 100%;
    }
    .sale-container {
        height: max-content;
        h3, p {
            margin: 20px 0;
            text-align: center;
        }
        background: #54afff;
        margin: 0;
        padding: 0;
        width: 100%;
    }
    .why-we-greed {
        margin-top: 30px;
    }
    .why-we-element {
        height: 200px;
    }
    .why-we-element:nth-of-type(1) h4,
    .why-we-element:nth-of-type(2) h4,
    .why-we-element:nth-of-type(3) h4 {
        margin-top: 200px;
    }
}
@media all and (max-width: 480px) {
    .number-logo {
        padding-top: 50px;
        padding-bottom: 20px;
        flex-direction: column;
        height: 100px;
        a {
            font-size: 20px;
        }
    }
    h1 {
        font-size: 28px;
    }
    .after-h1 {
        font-size: 22px;
    }
    .recalling {
        height: 120px;
        input {
            height: 35px;
        }
    }
    h2 {
        font-size: 23px;
        margin-top: 25px;
    }
    .why-we-greed {
        margin-top: 20px;
        margin-bottom: 90px;
    }
    .why-we-element {
        height: 180px;
    }
    .why-we-element:nth-of-type(1) h4,
    .why-we-element:nth-of-type(2) h4,
    .why-we-element:nth-of-type(3) h4 {
        margin-top: 200px;
    }
    .repear-devices-element:nth-of-type(1), 
    .repear-devices-element:nth-of-type(2), 
    .repear-devices-element:nth-of-type(3) {
        height: 170px;
    }
    .comfort-grid-element {
        height: 150px;
    }
    .footer-garanty-message {
        height: max-content;
    }
}
@media all and (max-width: 450px) {
    .repear-devices-element:nth-of-type(1), 
    .repear-devices-element:nth-of-type(2), 
    .repear-devices-element:nth-of-type(3) {
        background: white;
        height: 110px;
        p {
            text-align: center;
            line-height: 110px;
            margin: 0;
        }
    }
}
@media all and (max-width: 360px) {
    .comfort-grid-element {
        &:nth-of-type(1) {
            background: url(../img/parts.svg) center 100%/150px 100px no-repeat; 
            margin-bottom: 30px;
        }
    }
    .why-we-greed {
        margin-bottom: 110px;
    }
    .comford-grid {
        margin-top: 20px;
    }
}
@media all and (max-width: 340px) {
    .after-h1 {
        font-size: 20px;
    }
}