// _________________________________________________________________________-   rewivs
.reviews-slider {
  display: flex;
  flex-direction: row;
  width: 2240px;
  transition: 0.2s ease-in;
}
.raiting {
  width: 240px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: row-reverse;
}
.rait-star {
  overflow: visible;
  width: 40px;
  height: 50px;
  transform: scale(0.2);
  fill:#fff;
  stroke:#000;
  stroke-width: 5px;
  stroke: black; 
  stroke-width: 2px;
}
.raitingStar {
  display: block;
  flex-direction: row;
  width: 50px;
  height: 50px;
  background: center / contain no-repeat url(../img/star.svg);
  cursor: pointer;
}
.make-review-own {
  display: block;
  margin: 20px auto 0;
  width: 300px;
  height: 50px;
  background-color: rgba(0, 0, 255, 0.305);
  border: 2px solid white;
  border-radius: 20px;
  cursor: pointer;
  opacity: 0.8;
}
.make-review-own:hover {
  opacity: 1;
}
.allstars .raitingStar:hover,
.allstars .raitingStar:hover ~ .raitingStar {
  background: center / contain no-repeat url(../img/star-gold.svg);
}
.reviews-container {
  width: 320px;
  display: flex;
  justify-content: center;
}
.reviews-element {
  width: 260px;
  text-align: center;
  /* background: rgba(0, 0, 255, 0.105); */
  box-shadow: 0 0 5px rgba(0, 0, 0, .5);
  border-radius: 10px;
  padding: 20px 10px;
}
.reviews-element h4 {
  font-size: 18px;
  font-weight: 600;
  padding: 7px 0;
}
.slider-control {
  position: relative;
  height: 30px;
  width: 100%;
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.control-left {
  position: absolute;
  top: 0px;
  left: 10px;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50px;
  height: 30px;
  /* background: rgb(255,215,0);
  background: linear-gradient(90deg, rgba(255,215,0,1) 0%, rgba(2,0,36,1) 100%); */
  /* box-shadow: 0 0 5px rgb(0 0 0 / 50%); */
  opacity: 0.7;
  cursor: pointer;
  z-index: 10;
}
.move-ctr-right,
.move-ctr-left {
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 50;
}
.control-left:hover {   
  opacity: 1;
}
.control-left img {
  width: 40px;
  height: 30px;
  transform: rotate(180deg);
}
.control-right {
  position: absolute;
  top: 0px;
  right: 10px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 50px;
  height: 30px;
  /* box-shadow: 0 0 5px rgb(0 0 0 / 50%); */
  /* background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,215,0,1) 100%); */
  opacity: 0.7;
  cursor: pointer;
  z-index: 10;
}
.control-right:hover {
  opacity: 1;
}
.control-right img {
  width: 40px;
  height: 30px;
}